"use client";

import { useEffect, useState } from "react";
import HealthMetrics from "@/components/HealthMetrics";
import IndustryAnalytics from "@/components/IndustryAnalytics";
import styles from "@/styles/dashboard.module.scss";

export default function Dashboard() {
  const [connected, setConnected] = useState(false);
  const [metrics, setMetrics] = useState<any>(null);

  useEffect(() => {
    // Try WebSocket connection
    const ws = new WebSocket("ws://localhost:3002");

    ws.onopen = () => {
      setConnected(true);
    };

    ws.onmessage = (event) => {
      try {
        const data = JSON.parse(event.data);
        setMetrics(data);
      } catch (e) {
        console.error("Failed to parse metrics:", e);
      }
    };

    ws.onerror = () => {
      setConnected(false);
      // Fallback: fetch metrics via HTTP
      fetchMetrics();
    };

    ws.onclose = () => {
      setConnected(false);
    };

    return () => ws.close();
  }, []);

  const fetchMetrics = async () => {
    try {
      const res = await fetch("http://localhost:3002/metrics");
      if (res.ok) {
        const data = await res.json();
        setMetrics(data);
      }
    } catch (e) {
      console.error("Failed to fetch metrics:", e);
    }
  };

  return (
    <div className={styles.dashboard}>
      <header className={styles.header}>
        <h1>Health Monitor Dashboard</h1>
        <div className={styles.status}>
          <span className={`${styles.indicator} ${connected ? styles.connected : styles.disconnected}`}></span>
          {connected ? "Live" : "Offline"}
        </div>
      </header>

      <div className={styles.container}>
        {metrics ? (
          <>
            <HealthMetrics metrics={metrics} />
            <IndustryAnalytics />
          </>
        ) : (
          <div className={styles.loading}>
            <p>Connecting to health monitor...</p>
          </div>
        )}
      </div>
    </div>
  );
}
