"use client";

import { useEffect, useState } from "react";
import styles from "@/styles/metrics.module.scss";

interface MetricsData {
  uptime: number;
  memory: {
    used: number;
    total: number;
    percentage: number;
  };
  timestamp: number;
  pageViews: number;
  alerts: any[];
}

export default function HealthMetrics({ metrics }: { metrics: MetricsData }) {
  const [formattedUptime, setFormattedUptime] = useState("");

  useEffect(() => {
    const hours = Math.floor(metrics.uptime / 3600);
    const minutes = Math.floor((metrics.uptime % 3600) / 60);
    const seconds = metrics.uptime % 60;
    setFormattedUptime(`${hours}h ${minutes}m ${seconds}s`);
  }, [metrics.uptime]);

  const memoryPercent = metrics.memory?.percentage || 0;
  const memoryUsed = (metrics.memory?.used / (1024 * 1024 * 1024)).toFixed(2);
  const memoryTotal = (metrics.memory?.total / (1024 * 1024 * 1024)).toFixed(2);

  return (
    <div className={styles.metricsGrid}>
      <div className={styles.card}>
        <h3>System Status</h3>
        <div className={styles.metric}>
          <label>Uptime</label>
          <span className={styles.value}>{formattedUptime}</span>
        </div>
        <div className={styles.metric}>
          <label>Memory Usage</label>
          <div className={styles.progressBar}>
            <div
              className={styles.progress}
              style={{
                width: `${memoryPercent}%`,
                backgroundColor:
                  memoryPercent > 80 ? "#ff66ff" : "#00ffaa",
              }}
            ></div>
          </div>
          <span className={styles.value}>
            {memoryUsed}GB / {memoryTotal}GB ({memoryPercent.toFixed(1)}%)
          </span>
        </div>
      </div>

      <div className={styles.card}>
        <h3>Activity</h3>
        <div className={styles.metric}>
          <label>Page Views</label>
          <span className={styles.value}>{metrics.pageViews || 0}</span>
        </div>
        <div className={styles.metric}>
          <label>Active Alerts</label>
          <span className={styles.value}>{metrics.alerts?.length || 0}</span>
        </div>
        <div className={styles.metric}>
          <label>Last Update</label>
          <span className={styles.value}>
            {new Date(metrics.timestamp).toLocaleTimeString()}
          </span>
        </div>
      </div>
    </div>
  );
}
